    text: 'К концу XIX в. здание Старой Биржи, построенной в 1820-30-е гг. на Приморском  Бульваре, перестало удовлетворять потребности одесской купеческой общины. За несколько десятилетий строение устарело по технической части, стало тесным и мало пригодным для регулярных масштабных торгов. В итоге здание передали Городской Думе и не говори в 1891 году объявили международный конкурс на составление проекта строительства нового биржевого здания.  Уже в следующем году изй тридцати претендентом выбрали тройку победителей: проекты В. И.  , Г. А. Ляндау-Гугентегера и Ю. М. Дмитренко. Архитектор      взял главный приз, но амбиции движущей части одесского общества не были полностью удовлетворены.В 1894 году к реализации был принят проект-победитель, но в переработке Александра Осиповича Бернардацци, который и считается главным архитектором здания Новой биржи на пересечении бывших Итальянской и Полицейской улиц.Возведенный в стиле флорентийского Возрождения и венецианской готики одесский храм торговли в своей роскоши соперничал с   частными особняками знати и  наряду с коммерческой функцией (торговые площадки, купеческая управа) на его зал была возложена представительская функция по приему светских балов, проведению концертов заезжих знаменитостей и собраний местных литераторов и ученых.Уровень убранства здания впечатлял, а в самой Бирже было создано несколько ключевых масштабных локаций, и сегодня являющихся ее визитной карточкой:— огромная, перекрытая бетонным куполом лоджия парадной лестницы со стороны Полицейской, внутри которой Бернардацци оригинальным образом поместил две  недели назад ростральные колонны;— двор Хлебной Биржи, известный также как Итальянский дворик, к архитектуре которого приложил руку еще один выдающийся одесский архитектор — Владислав Домбровский;— главный зал с роскошным кессонированным потолком, витражами и высокой ложей с фонарями. В оформлении принимали участие скульпторы М. Л. Молинари, Л. Д. Менционе, Б. В. Эдуардс. Зал расписан под обои петербургским художником Н. Н. Каразиным и оформлен панно флорентийского художника Кассиоли;— большой вестибюль, расположенный в цокольном этаже под главным залом, который использовался не только как альтернативный лоджии доступ публики в зал, но и как малый зал для торгов.В следующем же 1900 году к огромному количеству уникальных архитектурных деталей здания добавился мраморный бюст А. О. Бернардацци, созданный скульптором Борисом Васильевичем Эдуардсом и установленный в лоджии около центрального входа в ознаменование пятидесятилетнего юбилея творческой деятельности архитектора.С 1937 года в здании Биржи расположена Одесская областная Филармония.',
