    text: 'Монументальное здание Общества Взаимного кредита возведено архитектором Бернардацци в 1903 г. под влиянием традиционных форм флорентийского Возрождения. Это одно из немногих банковских зданий в творчестве зодчего.Архитектурный образ ОВК оказал огромное влияние на формирование ансамбля перекрестка Пушкинской и Греческой. В 1905 г. архитектор Ю. М. Дмитренко соорудил на противоположной стороне Греческой здание Учетного банка, с которым творение Бернардацци составило в своем роде комплекс — своеобразные пропилеи въезда в деловую часть Одессы по Греческой.Здание решено трехобъемным и состоит из более высокой угловой части с малыми операционными залами и крыльями пониже, растянувшимися по Пушкинской и Греческой. Такое решение не свойственно для Бернардацци, предпочитавшего более простые, одно- и двухобъемные компоновки строений.Наиболее впечатляющим из технических решений является массивный фундамент, в опоры которого были врезаны сейфы.',
